	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>ðŸŒ¡ 9 â„ƒ</strong></p>\n<p>Yesterday is half a don, so I made a plastic model that I got as a sunbird in New Year since I came back from school. It is an elaborate model car model car. It was very difficult and I could not make it on time today.</p>\n<p>During the production, using paint and adhesive, thinner odor did not quite disappear even if it ventilated, I felt sick on the way and was working while pinching a break, so I did not proceed more than I thought.</p>\n<p>I finally abandoned around 19:30 and finally restarted the next morning.</p>\n<p>I went out to dinner with my family for lunch, but my parents told me that they smelled badly. Apparently thinner odor seems to have stuck in your body.</p>","frontmatter":{"cover":null,"date":"1991-02-03T21:15:00+09:00","strDate":"1991/2/3","title":"diary"}}},"pathContext":{"path":"/en/blog/1991/02/03-diary/","langKey":"en"}}

